@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  /* scroll-behavior: smooth; */
}

a {
  color: rgb(226, 226, 226);
  text-decoration: none;
}

li {
  list-style: none;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: #001f3f;
}

nav {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: #001f3f;
}

.logo {
  color: rgb(226, 226, 226);
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 20px;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  width: 40%;
}
.nav-links > li {
  cursor: pointer;
}

@-webkit-keyframes fallAnimation {
  0% {
    opacity: 0;
    top: -40px;
  }

  100% {
    top: 0px;
  }
}

@keyframes fallAnimation {
  0% {
    opacity: 0;
    top: -40px;
  }

  100% {
    top: 0px;
  }
}
@-webkit-keyframes upAnimation {
  0% {
    opacity: 0;
    bottom: -40px;
  }

  100% {
    bottom: 0px;
  }
}
@keyframes upAnimation {
  0% {
    opacity: 0;
    bottom: -40px;
  }

  100% {
    bottom: 0px;
  }
}

.nav-links__up-animation {
  -webkit-animation: upAnimation 800ms ease;
          animation: upAnimation 800ms ease;
}

.nav-links__down-animation {
  -webkit-animation: fallAnimation 800ms ease;
          animation: fallAnimation 800ms ease;
}

.nav-links a {
  letter-spacing: 3px;
  font-weight: bold;
  font-size: 14px;
  position: relative;
}

.nav-links__hover-effect--white::after {
  content: "";
  position: absolute;
  right: 0%;
  bottom: -3px;
  height: 3px;
  width: 0%;
  background-color: rgb(226, 226, 226);
  transition: all 350ms ease;
}

.nav-links__hover-effect--white:hover:after {
  width: 98%;
  left: 0;
}

