.showcase {
  display: flex;
  max-height: 70vh;
}

.row {
  padding: 5% 7%;
  background-color: #001f3f;
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
}

/* Intorduction part */
.showcase__name {
  opacity: 1;
  font-size: 4rem;
  font-weight: bold;
  padding-bottom: 1%;
  color: rgb(226, 226, 226);
}

.showcase__para {
  font-size: 1.4rem;
  color: rgb(226, 226, 226);
  padding: 0 0 2% 0;
}

.showcase__btn {
  width: 164px;
  font-size: 20px;
  color: #242424;
  background-color: rgb(226, 226, 226);
  border-style: none;
  padding: 10px 24px;
  border-radius: 2px;
  cursor: pointer;
  transition: all 350ms ease;
  position: relative;
}

.showcase__btn:hover {
  background-color: #083358;
  color: rgb(226, 226, 226);
  border: 0.2px white solid;
}

.showcase__button--popper {
  position: absolute;
  left: 480px;
}

.showcase__btn:hover .showcase__button--popper {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

/* Media */

.media-container {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 15% 0 0 0.5%;
  background-color: #001f3f;
}
.fa-linkedin,
.fa-github,
.fa-file-pdf {
  width: 60px !important;
  color: rgb(226, 226, 226);
}

.media-container__list-item {
  font-size: 4rem;
  color: black;
  transition: all 350ms ease;
}

.media-container__list-item:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

/* Media queries */

@media screen and (max-width: 1043px) {
  .showcase {
    flex-direction: column;
    align-self: center !important;
  }

  .row {
    text-align: center;
  }
  .showcase__name {
    font-size: 2.4rem !important;
  }

  .media-container {
    margin: 0 auto !important;
    order: 1;
    display: flex;
    flex-direction: column;
    margin-bottom: 3rem !important;
  }

  .media-container__list {
    display: flex;
    flex-direction: row;
  }

  .media-container__list-item {
    padding: 0 10px;
  }

  .showcase__name {
    padding-bottom: 5rem;
  }

  .showcase__para {
    display: none;
  }

  .showcase__btn {
    margin-bottom: 5rem;
  }
}

/* percentages does not work in styling */

.projects {
  margin-top: 4%;
  /* padding: 5%; */
  width: 100%;
  background-color: rgb(226, 226, 226);
}

.projects_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2%;
  width: 100%;
  /* margin: 10rem; */
}

.projects__heading {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 3rem;
}

.projects_list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.projects__item {
  display: flex;
  box-shadow: 8px 5px 14px 2px rgba(0, 0, 0, 0.86);
  max-width: 1400px;
  width: 1400px;
  height: 550px;
  position: relative;
  overflow: hidden;
  margin: 0 auto 9% auto;
}

.projects__item__wrapper:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  /* background-color: #1c1d25; */
  transition: all 350ms ease;
}

.projects__item:hover > .projects__item__wrapper:before {
  right: 0;
  width: 140%;
  opacity: 1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.projects__item_image {
  width: 100%;
  height: 100%;
  object-fit: fit;
  transition: all 300ms ease;
}

.projects__item:hover .projects__item_image {
  opacity: 0.7;
  -webkit-filter: blur(6px);
          filter: blur(6px);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.projects__item_info {
  position: absolute;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  top: 30%;
  margin-left: 55px;
  opacity: 0;
  transition: all 200ms ease;
}

.projects__item_info h2 {
  padding-top: 10px;
  font-weight: lighter;
}

.projects__index {
  color: black;
  transition: all 300ms ease;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
  font-size: 100px;
  position: absolute;
  top: 0;
  right: 0;
  padding-right: 22px;
}

.projects__item button {
  transition: all 200ms ease;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);

  opacity: 0;
  width: 190px;
  position: absolute;
  right: 100px;
  bottom: 60px;
  background-color: #001f3f;
  color: rgb(226, 226, 226);
}

.projects__item:hover .projects__item_info {
  opacity: 1;
  z-index: 2;
  -webkit-transform: translateY(20%);
          transform: translateY(20%);
}

.projects__item:hover button {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.projects__item:hover .projects__index {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

/* media queries */

@media screen and (max-width: 1300px) {
  .projects__item {
    width: 800px;
    max-width: none !important;
  }
}

@media screen and (max-width: 990px) {
  .projects__item {
    width: 680px !important;
  }
}

@media screen and (max-width: 930px) {
  .projects__item {
    width: 580px !important;
  }

  .projects__item:hover .projects__item_info {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
}

@media screen and (max-width: 768px) {
  .projects__item {
    width: 350px !important;
  }

  .projects__item_info {
    font-size: 14px;
  }
  .projects__item:hover .projects__item_info {
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
  }

  .projects__item button {
    right: 1.5rem;
    bottom: 1.8rem;
  }

  .projects__index {
    font-size: 70px;
  }
}

